import React from "react"
import { Button } from "reactstrap"
import { HApi, HQuery } from "src/core/helpers"
import MyButtonExt from "./ext"
import { fnListMyPage } from "src/helpers/fnList"
class MyButtonLink extends MyButtonExt {
  componentDidMount() {}
  render() {
    const { oneButton } = this.props
    // console.warn("render Button api", this.props)
    return (
      <Button
        {...this.getMorePropsButton()}
        {...oneButton.getMorePropsButton()}
        onClick={() => {
          window.print()
        }}
      >
        {oneButton.getTitleDisplay()}
      </Button>
    )
  }
}

export default MyButtonLink
