import { OneButton } from "@macashipo/mlib"
import React from "react"
import { Button } from "reactstrap"
import { KMoreOfButton } from "src/helpers/Flows/Button"
import { fnListMyPage, fnListUI } from "src/helpers/fnList"
interface IProps {
  oneButton: OneButton
  fnList?: any
}
class MyButtonExt extends React.Component<IProps> {
  constructor(props) {
    super(props)
    this.state = {}
    const { oneButton } = this.props
    oneButton.setFromComponent(this)
  }
  componentDidMount() {}
  onClick = () => {}
  parseResponseWhenSuccess = (res: any) => {
    const { oneButton, fnList } = this.props
    if (oneButton._configButton.NeedReload) {
      fnListMyPage.fnReloadPage(fnList)
    } else if (oneButton._configButton.NeedReloadList) {
      fnListMyPage.fnReloadList(fnList)
    }
  }
  getMorePropsButton = () => {
    const { oneButton } = this.props
    let _moreProps: any = {}
    if (oneButton && oneButton._moreObj) {
      if (oneButton._moreObj[KMoreOfButton.title]) {
        _moreProps.title = oneButton._moreObj[KMoreOfButton.title]
      }
    }
    return _moreProps
  }
  checkConfirmMsg = cb => {
    const { oneButton } = this.props
    //test
    // oneButton._configButton.ConfirmMsg = "Hello"
    if (oneButton && oneButton._configButton.ConfirmMsg) {
      fnListUI.fnshowConfirm({
        title: "Confirm",
        msg: oneButton._configButton.ConfirmMsg,
        onClickOK: () => {
          if (cb) {
            cb()
          }
        },
      })
    } else {
      if (cb) {
        cb()
      }
    }
  }
  render() {
    const { oneButton } = this.props
    return (
      <Button
        {...oneButton.getMorePropsButton()}
        onClick={() => {
          this.onClick()
        }}
      >
        {oneButton.getTitleDisplay()}
      </Button>
    )
  }
}

export default MyButtonExt
