import { ApiGeneric } from "src/services/apiServices"
import { fnListMyPage } from "src/helpers/fnList"
import { HUtils } from "@macashipo/mlib"
export const KMoreOfButton = {
  apiCheckShowModal: "apiCheckShowModal",
  acceptType: "acceptType", //upload
  apiUrlAfter: "apiUrlAfter", //upload
  title: "title", //show title cua button
  tooltip: "tooltip", //show tooltip cua button, chua su dung
}

export const FlowCheckShowModal = ({
  oneButton,
  fnList,
  customQuery,
  cb,
} = {}) => {
  if (oneButton) {
    let _configCheckShow = oneButton._moreObj[KMoreOfButton.apiCheckShowModal]
    if (_configCheckShow) {
      oneButton.changeIsLoading(true)
      let _request = {
        method: "POST",
        url: _configCheckShow.url,
        path: _configCheckShow.path,
        name: _configCheckShow.name,
      }
      ApiGeneric({
        request: _request,
        data: oneButton.getApiQuery({
          ...fnListMyPage.fnGetSharedRequestData(fnList),
          ...customQuery,
        }),
        successCallBack: response => {
          console.warn("response", response)
          let _config = HUtils.get(response, "Data.Config")
          if (_config && _config.IsShow) {
            if (cb) {
              cb({
                result: true,
                data: _config.FormData || {},
              })
            }
          } else {
            if (cb) {
              cb({
                result: false,
              })
            }
          }
          oneButton.changeIsLoading(false)
        },
        errorCallBack: (error, response) => {
          oneButton.changeIsLoading(false)
          if (cb) {
            cb({
              result: false,
            })
          }
        },
      })
    } else {
      if (cb) {
        cb({
          result: true,
        })
      }
    }
  } else {
    if (cb) {
      cb({
        result: true,
      })
    }
  }
}
